import PropTypes from 'prop-types';
import React from 'react';
import { Link } from 'react-router-dom';

const CaseFilesTableItem = ({data}) => {
  return (
    <tr>
      <td>{data.file.name}</td>
      <td>{data.status}</td>
      <td>{data.numberOfPages}</td>
      <td></td>
      <td>
        <Link
          to={`/file-details/${data.file.id}`}
          className="btn btn-primary btn-sm"
        >Details</Link>
      </td>
    </tr>
  );
}

CaseFilesTableItem.propTypes = {
  data: PropTypes.object.isRequired
}

export default CaseFilesTableItem;
