export const ActionTypes = {
  // // Fetch case list
  // FETCH_LIST_REQUEST: '[Case] Fetch list request',
  // FETCH_LIST_SUCCESS: '[Case] Fetch list success',
  // Fetch case list
  FETCH_FILES_REQUEST: '[Case] Fetch files request',
  FETCH_FILES_SUCCESS: '[Case] Fetch files success',
  // Fetch file by id
  FETCH_FILE_BY_ID_REQUEST: '[Case] Fetch file by id request',
  FETCH_FILE_BY_ID_SUCCESS: '[Case] Fetch file by id success',
  // Failure
  FAILURE: '[File] Failure'
};

// Fetch files
export const fetchFilesRequest = () => ({
  type: ActionTypes.FETCH_FILES_REQUEST
});
export const fetchFilesSuccess = data => ({
  type: ActionTypes.FETCH_FILES_SUCCESS,
  payload: data
});
// Fetch file by id
export const fetchFileByIdRequest = id => ({
  type: ActionTypes.FETCH_FILE_BY_ID_REQUEST,
  payload: id
});
export const fetchFileByIdSuccess = data => ({
  type: ActionTypes.FETCH_FILE_BY_ID_SUCCESS,
  payload: data
});
// Failure
export const failure = errorMessage => ({
  type: ActionTypes.FAILURE,
  payload: errorMessage
});
