import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { Bar } from 'react-chartjs-2';

import CaseFilesChartDetails from './CaseFilesChartDetails';

class CaseFilesChart extends Component {
  constructor(props){
    super(props);
    this.state = {
      data: {},
      filesByYear: [],
      isOpen: false
    }

    this.handleElementsClick = this.handleElementsClick.bind(this);
    this.handleToggle = this.handleToggle.bind(this);
  }

  componentWillMount(){
    const { caseFiles } = this.props;

    const attrs = this.filesToAttrs(caseFiles);
    const years = this.attrsToYearsAndPages(attrs);

    this.setState({
      attrs: attrs,
      years: years,
      data: {
        labels: years.map(item => item.year),
        datasets: [
          {
            label: 'Years',
            backgroundColor: 'rgba(255,99,132,0.2)',
            borderColor: 'rgba(255,99,132,1)',
            borderWidth: 1,
            hoverBackgroundColor: 'rgba(255,99,132,0.4)',
            hoverBorderColor: 'rgba(255,99,132,1)',
            data: years.map(item => item.pages.length)
          }
        ]
      }
    });
  }

  handleElementsClick(args){
    const { attrs, years } = this.state;
    if ( args.length ){
      let { year } = years[args[0]._index];
      this.setState({
        isOpen: true,
        filesByYear: this.attrsToFilesAndPages(attrs, year)
      });
    }
  }

  handleToggle(){
    this.setState({
      isOpen: false,
      filesByYear: []
    });
  }

  filesToAttrs(files){
    return files
      .map(item => item.attributes.map(attr => Object.assign({}, attr, {
        fileName: item.file.name,
        fileId: item.file.id
      })))
      .reduce((a, b) => a.concat(b));
  }

  attrsToYearsAndPages(attrs){
    let result = [];
    let map = new Map();

    for ( let i in attrs ){
      let attr = attrs[i];
      let year = new Date(attr.attribute.value).getFullYear();

      if ( !map.has(year) ){
        map.set(year, true);
        result.push({
          pages: attr.pages.map(item => `${attr.fileId}:${item}`),
          year: year
        });
      } else {
        result.map(item => {
          if (item.year === year){
            item['pages'] =
                [...item.pages].concat([...attr.pages].map(item => `${attr.fileId}:${item}`))
                .filter((elem, pos, arr) => arr.indexOf(elem) === pos);
          }
          return item;
        });
      }
    }
    return result;
  }

  attrsToFilesAndPages(attrs, selectedYear){
    let result = [];
    let map = new Map();

    for ( let i in attrs ){
      let attr = attrs[i];
      let year = new Date(attr.attribute.value).getFullYear();

      if ( selectedYear === year ){
        if ( !map.has(attr.fileId) ){
          map.set(attr.fileId, true);
          result.push({
            fileId: attr.fileId,
            fileName: attr.fileName,
            pages: attr.pages
          });
        } else {
          result.map(item => {
            if (item.fileId === attr.fileId){
              item['pages'] =
                [...item.pages].concat([...attr.pages])
                .filter((elem, pos, arr) => arr.indexOf(elem) === pos)
                .sort((a, b) => a - b);
            }
            return item;
          })
        }
      }
    }
    return result;
  }

  render(){
    const { data, isOpen, filesByYear } = this.state;
    return (
      <div className="area-container" style={{height: '800px'}}>
        <Bar
          data={data}
          options={{
            maintainAspectRatio: false
          }}
          onElementsClick={this.handleElementsClick}
        />
        <CaseFilesChartDetails isOpen={isOpen} onToggle={this.handleToggle} filesByYear={filesByYear} />
      </div>
    );
  }
}

CaseFilesChart.propTypes = {
  caseFiles: PropTypes.array.isRequired
}

export default CaseFilesChart;
