// Axios instance
// import axios from './axios.instance';
import axios from 'axios';
// Utils
import { errorHandler } from '../utils/request.handler';

export function $fetchFiles(){
  return axios('/shimlaw.json').catch(errorHandler);
}

export function $fetchFileById(id){
  console.log(id);
  return axios('/shimlaw.json')
    .then(response => response.data.find(item => item.file.id === id))
    .catch(errorHandler);
}
