import { takeLatest, call, put } from 'redux-saga/effects';

import {
  ActionTypes as Types,
  fetchFilesSuccess,
  fetchFileByIdSuccess,
  failure
} from '../actions/case.actions';

import { $fetchFiles, $fetchFileById } from '../../services/case.service';

function* fetchFiles(){
  try {
    const response = yield call($fetchFiles);
    yield put(fetchFilesSuccess(response.data));
  } catch(err){
    yield put(failure(err.message));
  }
}

function* fetchFileById(action){
  try {
    const response = yield call($fetchFileById, action.payload);
    // console.log(response);
    yield put(fetchFileByIdSuccess(response));
  } catch(err){
    yield put(failure(err.message));
  }
}

export function* caseWatcher(){
  yield takeLatest(Types.FETCH_FILES_REQUEST, fetchFiles);
  yield takeLatest(Types.FETCH_FILE_BY_ID_REQUEST, fetchFileById);
}
