import PropTypes from 'prop-types';
import React from 'react';

const FileDetailsTablePage = ({file}) => {
  let result = [];
  let pages = file.attributes
    .map(item => item.pages.map(i => i))
    .reduce((a, b) => a.concat(b))
    .filter((elem, pos, arr) => arr.indexOf(elem) === pos)
    .sort((a, b) => a - b);

  for ( let i in pages ){
    let page = pages[i];
    let years = [];
    for ( let j in file.attributes ){
      let attr = file.attributes[j];
      let attrValue = attr.attribute.value;

      if ( attr.pages.indexOf(page) !== -1 ){
        years.push(attrValue);
      }
    }
    result.push({
      page,
      years
    });
  }

  return (
    <table className="table">
      <thead className="thead-dark">
        <tr>
          <th scope="col">Page</th>
          <th scope="col">Date(s)</th>
        </tr>
      </thead>
      <tbody>
        {
          result.map((item, i) => (
            <tr key={i}>
              <td>{item.page}</td>
              <td className="_value">{item.years.join(', ')}</td>
            </tr>
          ))
        }
      </tbody>
    </table>
  )
}

FileDetailsTablePage.propTypes = {
  file: PropTypes.object.isRequired
};

export default FileDetailsTablePage;
