import {
  ActionTypes as Types
} from '../actions/case.actions';

const initialState = {
  caseList: [],
  caseFiles: [],
  fileDetails: {},

  fetching: false,
  fetched: false,
  error: false,
  errorMessage: ''
};

export default function(state = initialState, action){
  const { payload } = action;
  switch (action.type){
    case Types.FETCH_FILES_REQUEST:
    case Types.FETCH_FILE_BY_ID_REQUEST:
      return stateRequest(state);
    case Types.FETCH_FILES_SUCCESS:
      return stateSuccess(state, { caseFiles: payload });
    case Types.FETCH_FILE_BY_ID_SUCCESS:
      return stateSuccess(state, { fileDetails: payload });
    case Types.FAILURE:
      return stateFailure(state, { errorMessage: payload });
    default:
      return state;
  }
}

const stateRequest = (oldState) => {
  return Object.assign({}, oldState, { fetching: true, fetched: false, error: false, errorMessage: '' });
}

const stateSuccess = (oldState, newValue) => {
  return Object.assign({}, oldState, newValue, { fetching: false, fetched: true });
}

const stateFailure = (oldState, errorMessage) => {
  return Object.assign({}, oldState, { fetching: false, error: true, errorMessage })
}
