import PropsTypes from 'prop-types';
import React from 'react';
import { Modal, ModalBody, ModalFooter } from 'reactstrap';

const CaseFilesChartDetails  = ({isOpen, onToggle, filesByYear}) => {
  return (
    <Modal size="lg" isOpen={isOpen} toggle={onToggle}>
      <ModalBody>
        <table className="table">
          <thead className="thead-dark">
            <tr>
              <th scope="col">File</th>
              <th scope="col">Pages</th>
            </tr>
          </thead>
          <tbody>
            {
              filesByYear.map((item, index) => (
                <tr key={index}>
                  <td>{item.fileName}</td>
                  <td className="_value">{item.pages.join(', ')}</td>
                </tr>
              ))
            }
          </tbody>
        </table>
      </ModalBody>
      <ModalFooter>
        <button
          className="btn btn-sm btn-default"
          type="button"
          onClick={onToggle}>Close</button>
      </ModalFooter>
    </Modal>
  )
};

CaseFilesChartDetails.propTypes = {
  isOpen: PropsTypes.bool.isRequired,
  filesByYear: PropsTypes.array.isRequired,
  onToggle: PropsTypes.func.isRequired
}

export default CaseFilesChartDetails;
