import React, { Component, Fragment } from 'react'
// Store
import { connect } from 'react-redux';
import { fetchFilesRequest } from '../../store/actions/case.actions';
// Component
import { ProgressBar } from '../../components/ProgressBar';

import CaseFilesChart from './CaseFilesChart';
import CaseFilesTable from './CaseFilesTable';

class CaseFiles extends Component {
  constructor(props){
    super(props);

    this.state = {
      pageLoading: true
    }
  }
  componentWillMount(){
    this.props.fetchFiles();
    setTimeout(() => {
      this.setState({
        pageLoading: false
      })
    }, 2000);
  }
  render() {
    const { caseFiles } = this.props;
    const { pageLoading } = this.state;
    return (
      <Fragment>
        {
          pageLoading
          ? <ProgressBar />
          : 
            <div className="area area-page">
              <div className="container-fluid">
                <CaseFilesChart caseFiles={caseFiles} />
                <CaseFilesTable caseFiles={caseFiles} />
              </div>
            </div>
        }
      </Fragment>
    )
  }
}

const mapStateToProps = state => {
  return {
    caseFiles: state.case.caseFiles
  }
};

const mapDispatchToProps = dispatch => {
  return {
    fetchFiles: () => dispatch(fetchFilesRequest())
  }
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(CaseFiles);
