import React, { Component, Fragment } from 'react'
import { goBack } from 'connected-react-router';
// Store
import { connect } from 'react-redux';
import { fetchFileByIdRequest } from '../../store/actions/case.actions';
// Component
import { ProgressBar } from '../../components/ProgressBar';

import FileDetailsTableDate from './FileDetailsTableDate';
import FileDetailsTablePage from './FileDetailsTablePage';

class FileDetails extends Component {
  constructor(props){
    super(props);

    this.state = {
      pageLoading: true,
      activeTable: 1
    }

    this.handleBack = this.handleBack.bind(this);
    this.handleChangeTable = this.handleChangeTable.bind(this);
  }

  componentWillMount(){
    const { match } = this.props;
    this.props.fetchFileById(match.params.id);
    setTimeout(() => {
      this.setState({
        pageLoading: false
      })
    }, 2000);
  }

  handleBack(){
    this.props.goBack();
  }

  handleChangeTable = i => () => {
    this.setState({
      activeTable: i
    });
  }

  render() {
    const { pageLoading, activeTable } = this.state;
    const { fileDetails } = this.props;
    return (
      <Fragment>
        {
          pageLoading
          ? <ProgressBar />
          : 
            <div className="area area-page">
              <div className="container-fluid">
                <div className="form-group">
                  <button
                    className="btn btn-primary btn-sm"
                    type="button"
                    onClick={this.handleBack}
                  >
                    <i className="fa fa-chevron-left" aria-hidden="true" style={{'marginRight': '4px'}}></i>
                    Back
                  </button>
                </div>
                <div className="area-container">
                  <div className="btn-group btn-group-toggle" data-toggle="buttons">
                    <label className={`btn btn-secondary ${activeTable === 1 ? `active` : ``}`} onClick={this.handleChangeTable(1)} >
                      <input type="radio" name="options" id="option1" autoComplete="off" defaultChecked={activeTable === 1 ? true : false} />
                      Per date
                    </label>
                    <label className={`btn btn-secondary ${activeTable === 2 ? `active` : ``}`} onClick={this.handleChangeTable(2)}>
                      <input type="radio" name="options" id="option2" autoComplete="off" defaultChecked={activeTable === 2 ? true : false} />
                      Per page
                    </label>
                  </div>
                  <h4 className="mb-4 mt-4">{fileDetails.file.name}</h4>
                  {activeTable === 1 && <FileDetailsTableDate file={fileDetails} />}
                  {activeTable === 2 && <FileDetailsTablePage file={fileDetails} />}
                </div>
              </div>
            </div>
        }
      </Fragment>
    );
  }
}

const mapStateToProps = state => {
  return {
    fileDetails: state.case.fileDetails
  }
};

const mapDispatchToProps = dispatch => {
  return {
    fetchFileById: (id) => dispatch(fetchFileByIdRequest(id)),
    goBack: () => dispatch(goBack())
  }
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(FileDetails);
