import PropTypes from 'prop-types';
import React, { Fragment } from 'react';

const FileDetailsTableDate = ({file}) => {
  let result = [];
  let map = new Map();

  for ( let i in file.attributes ){
    let attr = file.attributes[i];
    let date = new Date(attr.attribute.value);
    let year = date.getFullYear();

    if ( !map.has(year) ){
      map.set(year, true);
      result.push({
        pages: attr.pages,
        year: year,
        date: date,
        name: attr.attribute.name || '-',
        text: attr.attribute.text || '-',
      });
    } else {
      result.map(item => {
        if (item.year === year){
          item['pages'] =
            [...item.pages].concat([...attr.pages])
            .filter((elem, pos, arr) => arr.indexOf(elem) === pos)
            .sort((a, b) => a - b);
        }
        return item;
      });
    }
  }

  return (
    <Fragment>
      <table className="table">
        <thead className="thead-dark">
          <tr>
            <th scope="col">Date</th>
            <th scope="col">Name</th>
            <th scope="col">Text</th>
            <th scope="col">Page(s)</th>
          </tr>
        </thead>
        <tbody>
          {
            result.map((item, i) => (
              <tr key={i}>
                <td style={{'whiteSpace': 'nowrap'}}>{`${item.date.getFullYear()}-${item.date.getMonth()}-${item.date.getDate()}`}</td>
                <td>{item.name}</td>
                <td>{item.text}</td>
                <td className="_value">{item.pages.join(', ')}</td>
              </tr>
            ))
          }
        </tbody>
      </table>
    </Fragment>
  )
}

FileDetailsTableDate.propTypes = {
  file: PropTypes.object.isRequired
};

export default FileDetailsTableDate;
